import Vue from 'vue';
import gbs from 'config/';
/*moment*/
import Moment from 'moment'
import { ajax, store } from 'utils/';
let stateObj = {
    bus: new Vue(), // 
    INTERFACE: gbs.json_path, //  
    IMAGE_PATH: gbs.image_path, // 
    UPLOAD_IMAGE: gbs.upload_path, // 
    store: store, //  vue
    moment: Moment // 
}
let state = function() {
    return (function(target, source) {
        for (var k in source) {
            target.prototype[k] = source[k];
        }
    })(Vue, stateObj)
}
export default state


